<template>
  <div class="container mt-5">
    <div class="row mt-5">
      <div class="col-3" v-observer:subtree="drop">
        <div class="p-2 alert alert-secondary" id="column1">
          <h3>Applicants</h3>
          <draggable
            class="list-group kanban-column"
            :list="main.arrApplicants"
            group="applicants"
          >
            <div
              class="list-group-item"
              v-for="element in main.arrApplicants"
              :key="element.name"
            >
              <span class="bold"><span class="title">
                {{ element.feedback_id }}
              </span></span>
              <span class="title">{{ element.title }}</span>
              <p><span class="bold"> Submittor:</span> {{element.submittor_username}}</p>
            </div>
          </draggable>
        </div>
      </div>

      <div class="col-3" v-observer:subtree="drop">
        <div class="p-2 alert alert-primary" id="column2">
          <h3>First Round</h3>
          <draggable
            class="list-group kanban-column"
            :list="main.arrFirstRound"
            group="applicants"
          >
            <div
              class="list-group-item"
              v-for="element in main.arrFirstRound"
              :key="element.name"
            >
              <span class="bold"><span class="title">
                {{ element.feedback_id }}
              </span></span>
              <span class="title">{{ element.title }}</span>
              <p><span class="bold"> Submittor:</span> {{element.submittor_username}}</p>
            </div>
          </draggable>
        </div>
      </div>

      <div class="col-3" v-observer:subtree="drop">
        <div class="p-2 alert alert-warning" id="column3">
          <h3>Second Round</h3>
          <draggable
            class="list-group kanban-column"
            :list="main.arrSecondRound"
            group="applicants"
          >
            <div
              class="list-group-item"
              v-for="element in main.arrSecondRound"
              :key="element.name"
            >
              <span class="bold"><span class="title">
                {{ element.feedback_id }}
              </span></span>
              <span class="title">{{ element.title }}</span>
              <p><span class="bold"> Submittor:</span> {{element.submittor_username}}</p>
            </div>
          </draggable>
        </div>
      </div>


      <div class="col-3" >
        <div class="p-2 alert alert-success" id="column4" >
          <h3>Final Decisions</h3>
          <draggable
            class="list-group kanban-column"
            :list="main.arrFinal"
            group="applicants"
          >
            <div
              class="list-group-item"
              v-for="element in main.arrFinal"
              :key="element.name"
            >
              <span class="bold"><span class="title">
                {{ element.feedback_id }}
              </span></span>
              <span class="title">{{ element.title }}</span>
              <p><span class="bold"> Submittor:</span> {{element.submittor_username}}</p>
            </div>

          </draggable>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import draggable from "vuedraggable";
import axios from "axios";
import io from "socket.io-client";
import observer from 'vue-mutation-observer';

const socket = io();

export default {
  name: "kanban-board",
  components: {
    draggable,
  },
  directives: {observer},
  data() {
    return {
      // for new tasks
      // 4 arrays to keep track of our 4 statuses
      main: {
        arrApplicants: [],
        arrFirstRound: [],
        arrSecondRound: [],
        arrFinal: [],
      }
    };
  },
  methods: {
    checkServer() {
      console.log("clicked!");
      socket.emit("connection", this.main);
    },
    checkDrop(e) {
      console.log("dragging");
      e.preventDefault();
    },
    drop() {
        const mainArr = {
          arrApplicants:this.main.arrApplicants,
          arrFirstRound:this.main.arrFirstRound,
          arrSecondRound:this.main.arrSecondRound,
          arrFinal:this.main.arrFinal
        };
        
         socket.emit("drop", mainArr)
         console.log(mainArr)
       }
  },
  created() {
    //socket.emit("connection", "hello insideman!");
    socket.on('reverse', (message) => {
	  this.position = message;
    })
    socket.on('updated_data', (data) => {
      console.log(data, "from INSIDEMAN")
      this.main = data;
})

    // document.getElementById("column4"),addEventListener("dragover", (e) => {
    //   e.preventDefault();
    // })

    // document.getElementById("column4").addEventListener("drop", (e) => {
    //   e.preventDefault();

    //   console.log("dropped!");
    //   socket.emit("drop", this.arrFinal);
    // })

  },
 
  mounted() {

    axios
      .get("https://mpengs.nyc/vue123")
      .then((response) => {
        console.log(response.data);

        if (response.data.length > 0) {
          for(var i = 0; i < response.data.length; i++) {
            if (response.data[i].work_status == "application") {
              this.main.arrApplicants.push(response.data[i]);
            }
            if (response.data[i].work_status == "step 1") {
              this.main.arrFirstRound.push(response.data[i]);
            }
            if (response.data[i].work_status == "step 2") {
              this.main.arrSecondRound.push(response.data[i]);
            }
            if (response.data[i].work_status == "complete") {
              this.main.arrFinal.push(response.data[i]);
            }
          }
        }
      })
      .then(console.log(this.data));
  },
};
</script>

